/*! @import "font-face"; */

@font-face {
	font-family: "eaststreet";
	src: /* woff2 particularly with everythingfonts.com/ttf-to-woff2 */
	url("../../../fonts/eaststreet-fontfacekit/everythingfonts/eaststreet.woff2?81tzjh") format("woff2"),
	url("../../../fonts/eaststreet-fontfacekit/icomoon/fonts/eaststreet.woff?81tzjh") format("woff"),
	url("../../../fonts/eaststreet-fontfacekit/icomoon/fonts/eaststreet.ttf?81tzjh") format("truetype");
	font-weight: normal;
	font-style: normal;
}

.es {
	font-family: "eaststreet";
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.es-mi-Home:before {
	content: "\eb6f";
}

.es-ion-qr-scanner:before {
	content: "\f346";
}

.es-mi-Cancel:before {
	content: "\ea80";
}

.es-mi-CellPhone:before {
	content: "\ea84";
}

.es-mi-ChevronDown:before {
	content: "\ea8e";
}

.es-mi-ChevronUp:before {
	content: "\ea97";
}

.es-mi-GlobalNavButton:before {
	content: "\eb48";
}

.es-mi-Like:before {
	content: "\ebb4";
}

.es-mi-More:before {
	content: "\ec15";
}

.es-mi-Repair:before {
	content: "\ecae";
}

.es-mi-Search:before {
	content: "\ecea";
}

.es-mi-Share:before {
	content: "\ecf8";
}

.es-mi-Up:before {
	content: "\ed7d";
	font-display: swap;
}

